<template>
  <el-form ref="form" :model="form" label-width="100px" class="body">
    <div class="based">
      <div class="image">
    <el-upload
  action="https://jsonplaceholder.typicode.com/posts/"
  list-type="picture-card"
  :on-preview="handlePictureCardPreview"
  :on-remove="handleRemove">
  <i class="el-icon-plus"></i>
    </el-upload>
    <el-dialog :visible.sync="form.dialogVisible">
      <img width="100%" :src="form.dialogImageUrl" alt="">
    </el-dialog>
    </div>
      <div class="left">
        <el-form-item label="学号:">
          <el-input v-model="form.id"></el-input>
        </el-form-item>
          <el-form-item label="姓名:">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="班级:">
          <el-input v-model="form.class"></el-input>
        </el-form-item>
      </div>
      <div class="right">
        <el-form-item label="性别:">
          <el-input v-model="form.sex"></el-input>
        </el-form-item>
          <el-form-item label="民族:">
          <el-input v-model="form.region"></el-input>
        </el-form-item>
        <el-form-item label="邮箱:">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label="QQ号:">
          <el-input v-model="form.qq"></el-input>
        </el-form-item>
      </div>
    </div>
  <el-form-item label="接受调配:">
    <el-radio-group v-model="form.resource">
      <el-radio label="同意" @click="resource=1"></el-radio>
      <el-radio label="不同意" @click="resource=0"></el-radio>
    </el-radio-group>
  </el-form-item>
  <div class="person">
  <el-form-item label="个人简介:">
    <el-input type="textarea" v-model="form.desc"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit">提交</el-button>
    <el-button @click="onCancel">取消</el-button>
  </el-form-item>
  </div>
</el-form>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          name: '',
          id: ' ',
          class: ' ',
          sex: ' ',
          region: '',
          qq: ' ',
          email: ' ',
          resource: '',
          desc: '',
          dialogImageUrl: '',
          dialogVisible: false
        }
      }
    },
    methods: {
      onSubmit() {
        console.log(this.form);
        alert("报名成功");
        this.$router.push('/signup');
      },
      onCancel() {
        this.$router.push('/signup')
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.body{
  margin: 0 auto;
  border: 100px;
  width: 800px;
  height: 100px;
}
.image{
  margin-left: 110%;
  top: 500px;
  border: 1px;
  height: 0px;
}
.based{
  flex-direction: row-reverse;
  width: 50%;
}

.person{
  width: 75%;
}
</style>
